    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

#content > .ships-container {
    padding-right: 5rem;
}

.board + .ships-container {
    padding-left: 5rem;
}

.ship {
    background: transparent;
    height: 50px;
    outline: dotted white;
    overflow: visible;
    position: relative;
    z-index: 5;
}

.placed-ship-on-board {
    pointer-events: none;
}

.biggest-ship {
    width: 250px;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center;
    background-size: 95%;
}

