___CSS_LOADER_EXPORT___.push([module.id, `#content {
    grid-row-start: 2;
    grid-row-end: 3;
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
}

.board {
    height: 500px;
    width: 500px;
    background-color: black;
}

.water-tile, .hit-water-tile, .hit-ship-tile, .friendly-ship-tile {
    position: relative;
    height: 50px;
    width: 50px;
    background-color: var(--water-tile-color);
    outline: dotted white;
}

.friendly-ship-tile {
    background: var(--friendly-ship-tile-color);
}

.hit-water-tile {
    background-color: var(--hit-water-tile-color);
}

.target-tile {
    background: var(--target-tile-color);
}

.water-tile:active {
    background: var(--active-target-tile-color);
}

.water-tile.no-active:active {
    background-color: var(--target-tile-color);
}

.hit-ship-tile {
    position: relative;
    background: var(--hit-ship-tile-color);
}

.explode {
    position: relative;
}

.hit-ship-tile.explode::after {
    content: "";
    left: 0;
    top: 0;
    position: absolute;
    height: 50px;
    width: 50px;
    background-size: contain;
}

.board {
    display: grid;
    grid-template: repeat(10, 50px) / repeat(10, 50px);
    position: relative;
}

.looser {
    animation-name: slideUp;
    animation-duration: 750ms;
    animation-fill-mode: both;
}

@keyframes slideUp {
    0% {
        transform: translate(0px, 0px);
    } 
    50% {
        transform: translate(0px, -50px);
    }
    100% {
        transform: translate(0px, 0px);
    } 
}

.board.active::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    background: black;
    z-index: 10;
    width: 500px;
    height: 500px;
}`, "",{"version":3,"sources":["webpack://./src/styles/boardStyle.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,eAAe;IACf,aAAa;IACb,SAAS;IACT,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,uBAAuB;AAC3B;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,yCAAyC;IACzC,qBAAqB;AACzB;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,6CAA6C;AACjD;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,0CAA0C;AAC9C;;AAEA;IACI,kBAAkB;IAClB,sCAAsC;AAC1C;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,OAAO;IACP,MAAM;IACN,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,wBAAwB;AAC5B;;AAEA;IACI,aAAa;IACb,kDAAkD;IAClD,kBAAkB;AACtB;;AAEA;IACI,uBAAuB;IACvB,yBAAyB;IACzB,yBAAyB;AAC7B;;AAEA;IACI;QACI,8BAA8B;IAClC;IACA;QACI,gCAAgC;IACpC;IACA;QACI,8BAA8B;IAClC;AACJ;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,OAAO;IACP,MAAM;IACN,iBAAiB;IACjB,WAAW;IACX,YAAY;IACZ,aAAa;AACjB","sourcesContent":["#content {\n    grid-row-start: 2;\n    grid-row-end: 3;\n    display: flex;\n    gap: 1rem;\n    justify-content: center;\n    align-items: center;\n}\n\n.board {\n    height: 500px;\n    width: 500px;\n    background-color: black;\n}\n\n.water-tile, .hit-water-tile, .hit-ship-tile, .friendly-ship-tile {\n    position: relative;\n    height: 50px;\n    width: 50px;\n    background-color: var(--water-tile-color);\n    outline: dotted white;\n}\n\n.friendly-ship-tile {\n    background: var(--friendly-ship-tile-color);\n}\n\n.hit-water-tile {\n    background-color: var(--hit-water-tile-color);\n}\n\n.target-tile {\n    background: var(--target-tile-color);\n}\n\n.water-tile:active {\n    background: var(--active-target-tile-color);\n}\n\n.water-tile.no-active:active {\n    background-color: var(--target-tile-color);\n}\n\n.hit-ship-tile {\n    position: relative;\n    background: var(--hit-ship-tile-color);\n}\n\n.explode {\n    position: relative;\n}\n\n.hit-ship-tile.explode::after {\n    content: \"\";\n    left: 0;\n    top: 0;\n    position: absolute;\n    height: 50px;\n    width: 50px;\n    background-size: contain;\n}\n\n.boa