  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
    box-sizing: border-box;
}

* {
    margin: 0;
}

body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}

input,
button,
textarea,
select {
    font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}

p {
    text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    text-wrap: balance;
}

#root,
#__next {
    isolation: isolate;
}

@font-face {
    font-family: 'ITC Machine';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

:root {
    --bg-color: hsl(0, 6%, 21%);
    --water-tile-color: hsl(205, 100%, 50%);
    --hit-water-tile-color: hsl(0, 0%, 80%);
    --target-tile-color: hsl(40, 100%, 50%);
    --active-target-tile-color: hsl(40, 100%, 25%);
    --hit-ship-tile-color: hsl(0, 100%, 25%);
    --friendly-ship-tile-color: hsl(137, 100%, 25%);
}

html, body {
    height: 100%;
}

body {
    background-color: var(--bg-color);
    display: grid;
    grid-template-rows: 1fr 3fr;
    font-family: 'ITC Machine';
    border: 5px double silver;
}

#header {
    margin-top: 5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.header-center {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

input {
    height: 75px;
    background: transparent;
    border: none;
    color: aliceblue;
    font-size: 4rem;
    padding-left: 8px;
    width: 300px;
    position: relative;
}

input:focus {
    background-color: var(--bg-color);
    position: relative;
    left: -8px;
    top: 10.5px;
    box-shadow: inset 0px 0px 25px black;
}

.input-wrapper {
    position: relative;
}

.name-span {
    position: absolute;
    top: 0;
    left: 0;
    height: 75px;
    background: transparent;
    border: none;
    color: aliceblue;
    font-size: 4rem;
    width: 300px;
}

.name-span::before {
    position: absolute;
    left: -8px;
    top: 0;
    content: var(--text-content);
    color: black;
    z-index: -1;
}

.buttons-container {
    display: flex;
    font-size: 1.75rem;
    gap: 1rem;
}

.header-button {
    padding-left: 1rem;
    padding-right: 1rem;
}

h1 {
    font-size: 7rem;
    color: azure;
    position: relative;
    letter-spacing: 1rem;
    text-align: center;
}

h1::before {
    position: absolute;
    left: -10px;
    top: 0;
    content: "BattleShip";
    color: black;
    z-index: -1;
}

#pass-turn-button {
    color: white;
    background-color: gray;
    position: absolute;
    z-index: 25;
    left: 44%;
    top: 60%;
    width: 150px;
    scale: 1.5;
}

#pass-turn-button:hover {
    background: #979393;
}

#pass-turn-button:active {
    background: #353333;
}

.pass-turn-button-text {
    position: relative;
    pointer-events: none;
}

.pass-turn-button-text.press-play::before {
    position: absolute;
    left: -1.5px;
    top: -1px;
    content: "PRESS PLAY";
    color: black;
    z-index: -1;
}

.pass-turn-button-text.pass-turn::before {
    position: absolute;
    left: -1.5px;
    top: -1px;
    content: "PASSED TURN";
    color: black;
    z-index: -1;
}

#pass-turn-button.dissappear {
    display: none;
}

video {
    position: absolute;
    height: 50px;
    width: 50px;
    z-index: 15;
    top: -30px;
    left: -2px;
    scale: 150%;
    pointer-events: none;
}

.stop-input {
    pointer-events: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/styles.css"],"names":[],"mappings":"AAAA;;;CAGC;;AAED;;;IAGI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,gBAAgB;IAChB,mCAAmC;AACvC;;AAEA;;;;;IAKI,cAAc;IACd,eAAe;AACnB;;AAEA;;;;IAII,aAAa;AACjB;;AAEA;;;;;;;IAOI,yBAAyB;AAC7B;;AAEA;IACI,iBAAiB;AACrB;AACA;;;;;;IAMI,kBAAkB;AACtB;;AAEA;;IAEI,kBAAkB;AACtB;;AAEA;IACI,0BAA0B;IAC1B,4CAAsE;AAC1E;;AAEA;IACI,2BAA2B;IAC3B,uCAAuC;IACvC,uCAAuC;IACvC,uCAAuC;IACvC,8CAA8C;IAC9C,wCAAwC;IACxC,+CAA+C;AACnD;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,iCAAiC;IACjC,aAAa;IACb,2BAA2B;IAC3B,0BAA0B;IAC1B,yBAAyB;AAC7B;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,iCAAiC;IACjC,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,oCAAoC;AACxC;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,gBAAgB;IAChB,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,MAAM;IACN,4BAA4B;IAC5B,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,YAAY;IACZ,kBAAkB;IAClB,oBAAoB;IACpB,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,MAAM;IACN,qBAAqB;IACrB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,sBAAsB;IACtB,kBAAkB;IAClB,WAAW;IACX,SAAS;IACT,QAAQ;IACR,YAAY;IACZ,UAAU;AACd;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,SAAS;IACT,qBAAqB;IACrB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,SAAS;IACT,sBAAsB;IACtB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,WAAW;IACX,UAAU;IACV,UAAU;IACV,WAAW;IACX,oBAAoB;AACxB;;AAEA;IACI,oBAAoB;AACxB","sourcesContent":["/*\n  Josh's Custom CSS Reset\n  https://www.joshwcomeau.com/css/custom-css-reset/\n*/\n\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\n\n* {\n    margin: 0;\n}\n\nb