___CSS_LOADER_EXPORT___.push([module.id, `dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    padding: 3rem;
    background-color: #4a4646;
    font-weight: bold;
    border: 5px double silver;
}

::backdrop{
    background: linear-gradient(gray 40% , black);
    opacity: 0.75;
}

#dialog-gamemode-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem;
}

#dialog-heading::before {
    position: absolute;
    left: -8px;
    top: 0;
    content: "Play against";
    color: black;
    z-index: -1;
}

.dialog-button {
    height: 5rem;
    width: 10rem;
    font-size: 2rem;
}

button {
    border: 5px double silver;
}

button:hover {
    background: #979393;
}

button:active {
    background: #353333;
}`, "",{"version":3,"sources":["webpack://./src/styles/dialog.css"],"names":[],"mappings":"AAAA;IACI,eAAe;IACf,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,SAAS;IACT,aAAa;IACb,yBAAyB;IACzB,iBAAiB;IACjB,yBAAyB;AAC7B;;AAEA;IACI,6CAA6C;IAC7C,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,MAAM;IACN,uBAAuB;IACvB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB","sourcesContent":["dialog {\n    position: fixed;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    margin: 0;\n    padding: 3rem;\n    background-color: #4a4646;\n    font-weight: bold;\n    border: 5px double silver;\n}\n\n::backdrop{\n    background: linear-gradient(gray 40% , black);\n    opacity: 0.75;\n}\n\n#dialog-gamemode-buttons {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 5rem;\n}\n\n#dialog-heading::before {\n    position: absolute;\n    left: -8px;\n    top: 0;\n    content: \"Play against\";\n    color: black;\n    z-index: -1;\n}\n\n.dialog-button {\n    height: 5rem;\n    width: 10rem;\n    font-size: 2rem;\n}\n\nbutton {\n    border: 5px double silver;\n}\n\nbutton:hover {\n    background: #979393;\n}\n\nbutton:active {\n    background: #353333;\n}"],"sourceRoot":""}]);
